
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.tab:after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    content: ' ';
    width: 100%;
    height: 0;
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.tab:after {
        -webkit-transform: scaleY(0.7);
        transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.tab:after {
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
}
}
.tab .tab-item {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 0px;
            flex: 1 1 0;
    height: 40px;
    text-align: center;
    line-height: 40px;
}
.tab .tab-item a {
      font-size: 14px;
      text-decoration: none;
      color: #4d555d;
}
.tab .tab-item a.active {
        color: #f01414;
}



