
.food-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 46px;
  width: 100%;
  background-color: #fff;
  overflow: hidden;
}
.food-wrapper.fade-enter, .food-wrapper.fade-leave-to {
    opacity: 0;
}
.food-wrapper.fade-enter-active, .food-wrapper.fade-leave-active {
    -webkit-transition: all .4s;
    transition: all .4s;
}
.food-wrapper .image {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.food-wrapper .image img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.food-wrapper .food-info-wrapper .food-info {
    position: relative;
    margin: 18px;
}
.food-wrapper .food-info-wrapper .food-info .name {
      font-size: 14px;
      font-weight: 700;
      color: 7, 17, 27;
      line-height: 14px;
}
.food-wrapper .food-info-wrapper .food-info .food-other {
      margin: 8px 0 18px;
      font-size: 0;
      color: #93999f;
      line-height: 10px;
}
.food-wrapper .food-info-wrapper .food-info .food-other .count, .food-wrapper .food-info-wrapper .food-info .food-other .rating {
        display: inline-block;
        font-size: 10px;
}
.food-wrapper .food-info-wrapper .food-info .food-other .count {
        margin-right: 12px;
}
.food-wrapper .food-info-wrapper .food-info .food-price {
      font-size: 0;
}
.food-wrapper .food-info-wrapper .food-info .food-price .price, .food-wrapper .food-info-wrapper .food-info .food-price .old-price {
        display: inline-block;
        line-height: 24px;
        font-weight: 700;
}
.food-wrapper .food-info-wrapper .food-info .food-price .price {
        font-size: 14px;
        color: #f01414;
}
.food-wrapper .food-info-wrapper .food-info .food-price .old-price {
        margin-left: 8px;
        font-size: 10px;
        color: #93999f;
        text-decoration: line-through;
}
.food-wrapper .food-info-wrapper .food-info .add-cart {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 74px;
      height: 24px;
      border-radius: 24px;
      font-size: 10px;
      color: #fff;
      line-height: 24px;
      text-align: center;
      background-color: #00a0dc;
}
.food-wrapper .food-info-wrapper .food-info .add-cart.toggle-leave-active, .food-wrapper .food-info-wrapper .food-info .add-cart.toggle-enter-active {
        -webkit-transition: width .4s;
        transition: width .4s;
}
.food-wrapper .food-info-wrapper .food-info .add-cart.toggle-leave-to {
        width: 24px;
}
.food-wrapper .food-info-wrapper .food-info .add-cart.toggle-enter {
        width: 24px;
}
.food-wrapper .food-info-wrapper .food-info .amount-wrapper {
      position: absolute;
      right: -6px;
      bottom: -6px;
}
.food-wrapper .food-desc-wrapper .food-desc {
    margin: 18px;
}
.food-wrapper .food-desc-wrapper .food-desc .title {
      margin-bottom: 6px;
      font-size: 14px;
      color: #07111b;
      line-height: 14px;
}
.food-wrapper .food-desc-wrapper .food-desc .desc {
      padding: 0 8px;
      font-size: 12px;
      color: #4d555d;
      line-height: 24px;
}
.food-wrapper .food-desc-wrapper .no-info {
    margin: 18px;
    font-size: 14px;
    color: #07111b;
    line-height: 14px;
}
.food-wrapper .title {
    margin-bottom: 6px;
    font-size: 14px;
    color: #07111b;
    line-height: 14px;
}
.food-wrapper .ratinglist .rating {
    padding: 16px 18px;
}
.food-wrapper .ratinglist .rating .header {
      position: relative;
      color: #93999f;
      line-height: 12px;
}
.food-wrapper .ratinglist .rating .header .user {
        position: absolute;
        right: 0;
        bottom: 0;
}
.food-wrapper .ratinglist .rating .header .user .username {
          display: inline-block;
          vertical-align: top;
}
.food-wrapper .ratinglist .rating .header .user .useravatar {
          width: 12px;
          height: 12px;
          border-radius: 50%;
}
.food-wrapper .ratinglist .rating .header .time {
        display: inline-block;
        font-size: 10px;
        color: #93999f;
        line-height: 12px;
}
.food-wrapper .ratinglist .rating .content {
      padding: 6px 0 16px;
      position: relative;
}
.food-wrapper .ratinglist .rating .content:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        content: ' ';
        width: 100%;
        height: 0;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.food-wrapper .ratinglist .rating .content:after {
            -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.food-wrapper .ratinglist .rating .content:after {
            -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
.food-wrapper .ratinglist .rating .content .icon-thumb_up, .food-wrapper .ratinglist .rating .content .icon-thumb_down {
        font-size: 12px;
        line-height: 24px;
}
.food-wrapper .ratinglist .rating .content .icon-thumb_up {
        color: #00a0dc;
}
.food-wrapper .ratinglist .rating .content .icon-thumb_down {
        color: #93999f;
}
.food-wrapper .ratinglist .rating .content .text {
        font-size: 12px;
        color: #07111b;
        line-height: 16px;
}



