
.seller-wrapper {
  position: absolute;
  top: 174px;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.seller-wrapper .seller .overview {
    padding: 18px 18px 0;
}
.seller-wrapper .seller .overview .overview-top {
      position: relative;
      position: relative;
}
.seller-wrapper .seller .overview .overview-top:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        content: ' ';
        width: 100%;
        height: 0;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.seller-wrapper .seller .overview .overview-top:after {
            -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.seller-wrapper .seller .overview .overview-top:after {
            -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
