}
.seller-wrapper .seller .overview .overview-top .content .sell-count, .seller-wrapper .seller .overview .overview-top .content .rating-count {
          font-size: 10px;
          color: #4d555d;
          line-height: 18px;
}
.seller-wrapper .seller .overview .overview-top .favorite {
        position: absolute;
