
.goods {
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
.goods .goods-type-wrapper {
    float: left;
    width: 80px;
    height: 100%;
    background-color: #f3f5f7;
}
.goods .goods-type-wrapper .goods-type-item {
      display: table;
      width: 56px;
      height: 54px;
      padding: 0 12px;
      font-size: 12px;
      font-weight: 200;
      line-height: 14px;
}
.goods .goods-type-wrapper .goods-type-item:not(:last-child) {
        position: relative;
}
.goods .goods-type-wrapper .goods-type-item:not(:last-child):after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          content: ' ';
          width: 100%;
          height: 0;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.goods .goods-type-wrapper .goods-type-item:not(:last-child):after {
              -webkit-transform: scaleY(0.7);
              transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.goods .goods-type-wrapper .goods-type-item:not(:last-child):after {
              -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
}
}
.goods .goods-type-wrapper .goods-type-item.current {
