
.rating-selector {
  padding: 18px 18px 0;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.rating-selector .rating-type {
    padding: 12px 18px 18px;
    font-size: 0;
    position: relative;
}
.rating-selector .rating-type:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      content: ' ';
      width: 100%;
      height: 0;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.rating-selector .rating-type:after {
          -webkit-transform: scaleY(0.7);
          transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.rating-selector .rating-type:after {
          -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
}
.rating-selector .rating-type .button {
      display: inline-block;
      margin-right: 8px;
      padding: 8px 12px;
      font-size: 12px;
      color: #4d555d;
}
.rating-selector .rating-type .button.active {
        color: #fff;
}
.rating-selector .rating-type .button.positive {
        background: rgba(0, 160, 220, 0.2);
}
.rating-selector .rating-type .button.positive.active {
          background: #00a0dc;
}
.rating-selector .rating-type .button.negative {
        background: rgba(77, 85, 93, 0.2);
}
.rating-selector .rating-type .button.negative.active {
          background: #4d555d;
}
.rating-selector .content-only {
    padding: 12px 0;
}
.rating-selector .content-only .icon-check_circle {
      display: inline-block;
      font-size: 24px;
      color: #93999f;
      line-height: 24px;
}
.rating-selector .content-only .icon-check_circle.active {
        color: #00c850;
}
.rating-selector .content-only .text {
      display: inline-block;
      margin-left: 4px;
      vertical-align: top;
      font-size: 12px;
      color: #93999f;
      line-height: 24px;
}



